// HEADER :: START

.header {
  z-index: 1000;
  position: sticky;
  top: 0;
  left: 0;
  background-color: #ffffff;
  border-bottom: 1px solid $c-light-periwinkle;

  .c-grid-fluid {
    padding-left: 25px;
    padding-right: 25px;
  }

  &__wrapper {
    display: flex;
    align-items: center;
    min-height: 87px;

    &-left {
      padding-right: 39px;
    }

    &-middle {
      flex: 1;
      max-width: 100%;
    }

    &-right {
      width: 100%;
      max-width: 314px;
    }
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'PT Sans', sans-serif;
    font-size: 14px;
    font-weight: bold;
    text-align: center;
    @include transition(border-color, background-color, color);

    &--mark {
      margin-right: 10px;

      .icon-font {
        font-size: 24px;
        color: #192E47;
      }
    }

    &--search {
      margin-left: 10px;

      .icon-font {
        font-size: 24px;
        color: #192E47;
      }
    }

    &--login {
      margin-right: 4px;
      width: 83px;
      height: 40px;
      border-radius: 21.5px;
      border: solid 1px #dbdfe4;
      background-color: #ffffff;
      color: #132e49;

      &:hover {
        border-color: #008192;
        background-color: #008192;
        color: #ffffff;
        @include transition(border-color, background-color, color);
      }
    }

    &--get {
      margin-left: 4px;
      width: 106px;
      height: 40px;
      border-radius: 21.5px;
      border: 1px solid #008192;
      background-color: #008192;
      color: #ffffff;

      &:hover {
        border-color: #dbdfe4;
        background-color: transparent;
        color: #132e49;
        @include transition(border-color, background-color, color);
      }
    }

    &-wrapper {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    &-left {
      position: relative;
      display: flex;
      align-items: center;
      padding-right: 24px;

      &:before {
        content: '';
        z-index: 0;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 0;
        width: 1px;
        height: 43px;
        background-color: #f6f6f6;
      }
    }

    &-right {
      display: flex;
      align-items: center;
      padding-left: 24px;
    }
  }

  &__nav {
    &-list {
      display: flex;
      align-items: center;
    }

    &-item {
      padding-left: 20px;
      padding-right: 20px;
    }

    &-link {
      position: relative;
      display: flex;
      align-items: center;
      font-family: 'PT Sans', sans-serif;
      color: #132e49;
      @include transition(color);

      &:before {
        content: '';
        opacity: 0;
        z-index: 0;
        position: absolute;
        bottom: -10px;
        left: 0;
        width: 100%;
        height: 2px;
        border-radius: 2.5px;
        background-color: #008192;
        @include transition(opacity);
      }

      &:hover {
        color: #008192;
        @include transition(color);
      }

      &.is-active {
        color: #008192;

        &:before {
          opacity: 1;
          @include transition(opacity);
        }
      }
      &--drop {
        .icon-corner {
          font-size: 6px;
          margin-left: 10px;
        }
      }

      &--more {}
    }
  }
}

// HEADER :: END